@import 'vue-cal/dist/vuecal.css';
:root {
  --iande-border-radius: 10px;
  --iande-primary-color: #A8DBBC;
  --iande-secondary-color: #1E2E55;
  --iande-tertiary-color: #7DB6C5;
  --iande-background-color: white;
  --iande-lighter-color: #F5F5F5;
  --iande-light-color: #DDDDDD;
  --iande-medium-color: #999999;
  --iande-text-color: #666666;
  --iande-error-color: #E53E3E;
  --iande-success-color: #238B19;
  --iande-text-font: Open Sans, sans-serif;
  --iande-title-font: Quicksand, Open Sans, sans-serif;
  --iande-accent-color: var(--iande-secondary-color);
}

.iande-admin-agenda__month {
  font-size: 1.2em;
  font-weight: 500;
  margin: 5px 10px;
}
.iande-admin-agenda__line {
  border-top: 1px solid #EEE;
  font-variant-numeric: tabular-nums;
  margin: 0 10px;
}
.iande-admin-agenda__event {
  align-items: center;
  color: rgba(0, 0, 0, 0.75);
  display: flex;
  flex-direction: column;
  height: 100%;
  justify-content: center;
  position: relative;
  text-align: center;
  width: 100%;
}
.iande-admin-agenda__event.publish {
  background-color: lightgreen;
}
.iande-admin-agenda__event.pending {
  background-color: lemonchiffon;
}
.iande-admin-agenda__event.pending:before {
  background-color: crimson;
  border-radius: 50%;
  box-shadow: 0 0 0.25em pink;
  content: "";
  height: 0.5em;
  position: absolute;
  right: 0.25em;
  top: 0.25em;
  width: 0.5em;
}
.iande-admin-agenda__event a {
  color: inherit;
  text-decoration: none;
}
.iande-admin-agenda__event svg[data-icon] {
  margin-left: 1ch;
}
.vuecal__cells.week-view .iande-admin-agenda__event > :nth-of-type(2) {
  display: none;
}
.vuecal__cells.week-view .iande-admin-agenda__event svg[data-icon] {
  display: none;
}
.iande-admin-agenda__group-link {
  font-weight: bold;
}
.iande-admin-agenda__counters {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
}
.iande-admin-agenda__counter svg[data-icon] {
  font-size: 0.875em;
}
.iande-admin-agenda__counter.publish svg[data-icon] {
  color: limegreen;
}
.iande-admin-agenda__counter.pending svg[data-icon] {
  color: orange;
}
.iande-admin-agenda__counter.pending:before {
  background-color: crimson;
  border-radius: 50%;
  box-shadow: 0 0 0.25em pink;
  content: "";
  height: 0.5em;
  position: absolute;
  right: 0.25em;
  top: 0.25em;
  width: 0.5em;
}
.iande-admin-agenda .vuecal__cells.month-view .vuecal__cell-content {
  justify-content: flex-start;
  position: relative;
}

.wp-admin .iande-status-metabox > * {
  margin-top: 15px !important;
}
.wp-admin .iande-status-metabox label {
  font-weight: bold;
  margin-bottom: 10px;
}
.wp-admin .iande-status-metabox textarea {
  width: 100%;
}
.wp-admin .iande-status-metabox button.button {
  display: block;
  width: 100%;
}
.wp-admin .iande-status-metabox button.button.button-cancel {
  background-color: #E53E3E;
  border-color: #E53E3E;
}
.wp-admin .iande-status-metabox button.button.button-cancel:hover {
  background-color: #d41c1c;
  border-color: #d41c1c;
}
.wp-admin .iande-status-metabox .form-error {
  color: #E53E3E;
}
.wp-admin .iande-itinerary-metabox {
  display: grid;
  grid-gap: 10px;
  grid-template-columns: auto 1fr;
}
.wp-admin .iande-itinerary-metabox .components-base-control__field {
  display: contents;
}
.wp-admin .meta-box-sortables .cmb2-wrap input[type=text],
.wp-admin .meta-box-sortables .cmb2-wrap select,
.wp-admin .meta-box-sortables .cmb2-wrap textarea {
  max-width: 580px;
  width: 100%;
}
.wp-admin .meta-box-sortables .cmb2-wrap summary {
  cursor: pointer;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat .cmb-repeat-row::before {
  padding-top: 7px;
  box-sizing: border-box;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat .cmb-repeat-row .cmb-remove-row-button {
  background-color: transparent;
  border: 2px solid #a00;
  border-radius: 50px;
  color: #a00;
  margin-top: 3px;
  padding: 0;
  height: 30px;
  width: 30px;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat .cmb-repeat-row .cmb-remove-row-button::before {
  box-sizing: border-box;
  padding: 4px;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat .cmb-repeat-row .cmb-remove-row-button:hover {
  color: #fff;
  background-color: red;
  border: 2px solid red;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat .alignleft {
  margin-right: 15px;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat .alignleft > p {
  display: none;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat select {
  margin-top: 0;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-repeat h3.cmbhandle-title {
  cursor: pointer;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-post-ajax-search-results {
  width: 100%;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-post-ajax-search-results li {
  max-width: 580px;
}
.wp-admin .meta-box-sortables .cmb2-wrap .cmb-post-ajax-search-results li a.edit-link {
  display: inline-block;
  padding: 5px 0;
  max-width: 90%;
  width: 95%;
}
