#iande-reports-page h1 {
  margin-bottom: 20px;
}
#iande-reports-page .date-range-fields {
  align-items: center;
  display: flex;
  margin-bottom: 20px;
}
@media screen and (max-width: 768px) {
  #iande-reports-page .date-range-fields {
    display: block;
  }
}
#iande-reports-page .date-range-fields > p {
  margin-right: 20px;
}
@media screen and (max-width: 768px) {
  #iande-reports-page .date-range-fields > p {
    font-size: 1.3em;
    font-weight: bold;
  }
}
#iande-reports-page .date-range-fields > div {
  align-items: center;
  display: flex;
}
@media screen and (max-width: 768px) {
  #iande-reports-page .date-range-fields > div {
    margin-bottom: 10px;
  }
}
#iande-reports-page .date-range-fields > div label {
  font-weight: bold;
  padding-left: 20px;
  padding-right: 10px;
}
@media screen and (max-width: 768px) {
  #iande-reports-page .date-range-fields > div label {
    width: 10%;
  }
}
#iande-reports-page .date-range-fields input {
  background-color: white;
}
#iande-reports-page .iande-charts-header {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}
@media screen and (max-width: 768px) {
  #iande-reports-page .iande-charts-header {
    display: block;
  }
}
#iande-reports-page .iande-chart-box {
  background-color: white;
  border: 1px solid #DDDDDD;
  display: flex;
  flex: 1;
  flex-direction: column;
  margin-right: 20px;
  padding: 20px 30px;
}
#iande-reports-page .iande-chart-box:last-child {
  margin-right: 0;
}
@media screen and (max-width: 960px) {
  #iande-reports-page .iande-chart-box {
    margin-bottom: 20px;
    margin-right: 0;
  }
}
#iande-reports-page .iande-chart-box__title {
  display: inline-block;
  font-size: 16px;
  font-weight: bold;
  margin-bottom: 20px;
}
@media screen and (min-width: 768px) and (max-width: 1080px) {
  #iande-reports-page .iande-chart-box__title {
    text-align: center;
  }
}
#iande-reports-page .iande-chart-box__content {
  align-items: center;
  display: flex;
  font-size: 24px;
  justify-content: space-between;
}
#iande-reports-page .iande-chart-box__content span {
  font-weight: bold;
}
#iande-reports-page .iande-chart-box__content svg[data-icon] {
  color: var(--iande-tertiary-color);
}

.iande-charts-grid {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr 1fr;
}
.iande-charts-grid .iande-chart-wrapper {
  background-color: white;
  border: 1px solid #DDDDDD;
  padding: 20px 30px;
}
.iande-charts-grid .iande-chart-wrapper.-colspan-2 {
  grid-column-end: span 2;
}
.iande-charts-grid .iande-chart-wrapper .apexcharts-toolbar {
  top: -40px !important;
}
.iande-charts-grid .iande-chart-wrapper .apexcharts-legend.position-top.apexcharts-align-left,
.iande-charts-grid .iande-chart-wrapper .apexcharts-legend.position-bottom.apexcharts-align-left {
  padding-left: 0;
}
.iande-charts-grid .iande-chart-wrapper .apexcharts-legend.position-top.apexcharts-align-left .apexcharts-legend-series,
.iande-charts-grid .iande-chart-wrapper .apexcharts-legend.position-bottom.apexcharts-align-left .apexcharts-legend-series {
  margin: 3px 10px 3px 0 !important;
  width: 100%;
}
.iande-charts-grid .iande-chart-wrapper .apexcharts-inner {
  margin: 100px auto;
}